import {
  setTokenStorage,
  getTokenStorage,
  configHandle
} from '@/test/tool.js'
import uniIcons from '@/components/uni-icons/uni-icons.vue'

export default {
	components: {
		uniIcons
		
	},
	data() {
		return {
			imageList:[],
			name:"",
			idCard:"",
			phone:"",
			 array: ['请选择', '男', '女'],
			   sexIndex: 0,
			   workList:[],
			   workIndex:0,
		userInfo:{
			avatar:"",
			username:"",
			idcard:"",
			sex:"0",
			phone:"",
			unit:"0"
		}
		};
	},
	onShow(){
	uni.hideHomeButton()
	},

onLoad() {
		
		//获取上报类型
		this.getType();
	},
	methods:{
		jumpRegster(){
			uni.redirectTo({
			    url: '../login/login',
				
			});
		},
		getType() {
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-dwxz.htm', { params: {type:1} })
				.then(res => {
				 //	console.log(res);
					if (res.data.msgState == 1) {
						var obj1 = [{ id: '0', unit: '点击选择' }];
						var data = res.data.list;
						var obj = [...obj1, ...data];
						this.workList = this.typeChange(obj);
						
					} else {
						uni.showLoading({
							title: '请求失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
			//处理函数
			typeChange(val){
				var arr=[]
				for (let item in val) {
					// console.log(val[item].unit)
					arr.push(val[item].unit)
				}
			
			return arr;
			
			},
		submitMsg(){
			console.log(this.userInfo)
			
			if(this.userInfo.avatar.length<1){
				uni.showModal({
					
					content: "请上传头像!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.username.length<1){
				uni.showModal({
					
					content: "请输入姓名!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.sex=='0'){
				uni.showModal({
					
					content: "请选择性别!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.idcard.length<10){
				uni.showModal({
					
					content: "请输入身份证号!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			
			if(this.userInfo.phone.length<5){
				uni.showModal({
					
					content: "请输入电话号码!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.unit=='0'){
				uni.showModal({
			
					content: "请选择所在单位!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			//整理数据
				
			var obj={
				avatar:this.userInfo.avatar,
				username:this.userInfo.username,
				idcard:this.userInfo.idcard,
				sex:this.userInfo.sex.toString(),
				phone:this.userInfo.phone,
				unit:this.userInfo.unit.toString()
			}
			
			console.log(obj)
			uni.showLoading({
				title: 'loading'
			});
			setTokenStorage('register')
			//发起请求
			this.$http.post('/interface/rest/http/xlwb/xlgc-wb-jdh-yhzc.htm',obj).then(res => {
				console.log(res);
				uni.hideLoading();
				if(res.data.msgState==1){
					uni.redirectTo({
					    url: '../regres/regres',
						
					});
				}
			}).catch(err => {
				console.log(err);
			})
			// uni.redirectTo({
			//     url: '../regres/regres',
				
			// });
		},
	
		
		//上传头像
		chooseImage(){
			uni.chooseImage({
				count:1,
				success: (res) => {
					 //console.log(JSON.stringify(res.tempFilePaths));
					uni.showLoading({
						title: '上传中...',
						mask: true
					})
					uni.uploadFile({
						url: 'https://app8848.com/interface/rest/file/upload/util/uploadFile.htm', //仅为示例，非真实的接口地址
						 header:{token:"311288512_"},
						filePath: res.tempFilePaths[0],
						name: 'file',
						
						success: (uploadFileRes) => {
							// console.log("图片上传+++++++")
							// console.log(JSON.parse(uploadFileRes.data));
							
							var datas=JSON.parse(uploadFileRes.data)
							if(datas.msgState==1){
								
								//将返回的数据存入img,并通知外界
								// 默认返回的有路径，字段名 url
								this.userInfo.avatar =datas.result;
								
							}else{
								uni.showToast({
								    title: '上传失败，请重新上传',
								    duration: 2000
								});
							}
							
						}
					});
					uni.hideLoading()
				},
				
			})
		},
		//选择单位
		workListChange(e){
			this.userInfo.unit = parseInt(e.detail.value)
			
		},
			
		sizeTypeChange(e){
			this.userInfo.sex = parseInt(e.detail.value)
			
		}
	}
}
